<template>
  <div>
    <Heads :title="languages.a12[langIndex]"></Heads>
    <main>
      <qrcode :floornum="encryption"></qrcode>
      <div class="floortext">
        <span>*{{languages.a25[langIndex]}}*</span>
      </div>
      <div class="copcode_btn">
        <van-button
          @click="back()"
          class="back_button"
          color="#7e7e7e"
          type="info"
        >{{languages.a6[langIndex]}}</van-button>
        <van-button
          @click="back()"
          class="back_button"
          color="#dc0000"
          type="info"
        >{{languages.a26[langIndex]}}</van-button>
      </div>
    </main>
  </div>
</template>

<style scoped>
main {
  margin-top: 3.2rem;
  margin-bottom: 2.5rem;
  width: 100%;
  overflow: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.floortext {
  padding: 0.4rem;
  font-size: 0.22rem;
  font-weight: 800;
  color: #dc0000;
}
.copcode_btn{
  width: 80%;
  display: flex;
  justify-content: space-around;
}
.back_button {
  width: 1.8rem;
  height: 0.8rem;
}
</style>

<script>
import Heads from "@/components/Head";
import qrcode from "@/components/QRCode";
export default {
  name: "Code",
  components: { Heads, qrcode },
  data() {
    return {
      encryption: ""
    };
  },
  methods: {
    back() {
      this.$router.back();
    }
  },
  computed: {
    languages() {
      return this.$store.state.languages;
    },
    langIndex() {
      return this.$store.state.langIndex;
    }
  },
  mounted() {
    this.encryption = this.$route.params.encryption;
  }
};
</script>